import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

export interface APIEntry {
  API: string;
  Description?: string;
  Link?: string;
}

export interface ContactState {
  contacts: APIEntry[];
  filteredName: APIEntry[];
  filter: string;
}

const baseUrl = "https://api.publicapis.org/entries";

const initialState: ContactState = {
  filter: "",
  contacts: [],
  filteredName: [],
};

export const fetchBase = createAsyncThunk<APIEntry[]>(
  "contacts/fetchBase",
  async () => {
    try {
      const { data } = await axios.get(baseUrl);
      const listInfo: APIEntry[] = data.entries.slice(10, 26);
      return listInfo;
    } catch (err) {
      console.log(err);
      throw err;
    }
  }
);

const contactSlice = createSlice({
  name: "contacts",
  initialState,
  reducers: {
    deleteItem: (state, action) => {
      state.contacts = state.contacts.filter(
        (contact) => contact.API !== action.payload
      );
    },
    handleChange: (state, action) => {
      state.filter = action.payload;
    },
  },
  extraReducers: (builder) => {
    builder.addCase(fetchBase.fulfilled, (state, action) => {
      if (state.contacts.length === 0) {
        state.contacts = action.payload;
      }
    });
  },
});

export const { deleteItem, handleChange } = contactSlice.actions;
export default contactSlice.reducer;
